<template>
  <div class="page page-trans-in-balance-from-blockchain">
    <van-cell-group>
      <img v-if="cryptoAccountInfo.address && isTrading" class="QRcode" alt="qr code" />
      <div class="notice">
        {{
          !form.paymentType
            ? "选择币种即可获取转币地址"
            : `此地址只可接受${form.paymentType}`
        }}
      </div>
      <van-cell title="币种">
        <van-field
          v-model="form.paymentType"
          @click="chooseCrypto"
          placeholder="请选择币种"
          readonly
          colon
          input-align="right"
        />
        <van-icon name="exchange" slot="right-icon" />
      </van-cell>
      <van-cell title="主网">
        <van-field value="Tronscan(TRC20)" readonly colon input-align="right" />
      </van-cell>
      <van-cell v-if="isTrading" title="转入地址">
        <van-field
          v-model="cryptoAccountInfo.address"
          readonly
          colon
          input-align="right"
        />
        <button
          v-clipboard="cryptoAccountInfo.address"
          class="copy copyBtn"
          @success="$root.copySuccessful"
          slot="right-icon"
        ></button>
      </van-cell>
      <van-cell :title="form.paymentType ? `转入数量(${form.paymentType})` : '转入数量'">
        <van-field
          v-model="form.amount"
          :formatter="(v) => (isTrading ? parseFloat(v).toFixed(2) : v)"
          :readonly="isTrading"
          placeholder="请输入转入数量"
          colon
          input-align="right"
        />
      </van-cell>
      <van-cell :title="ratio">
        <van-field
          :value="`转入金额:${howMuchRMB}`"
          readonly
          colon
          input-align="right"
        >
          <template #extra> V币 </template>
        </van-field>
      </van-cell>
    </van-cell-group>

    <CryptoTradingAnnouncement />

    <van-popup v-model="showPicker" round position="bottom">
      <van-picker
        show-toolbar
        :columns="columns"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>

    <van-button
      v-if="isTrading"
      class="primary block"
      :to="`/crypto_order_detail/${orderInfo.orderNo}`"
    >
      查看订单
    </van-button>
    <van-button v-else class="primary block" @click="businessDeposit">
      确定转入
    </van-button>
  </div>
</template>

<script>
import { businessDeposit } from "@/apis/crypto";
import CryptoTradingAnnouncement from "@/components/CryptoTradingAnnouncement.vue";
import { CRYPTO_WEB } from "@/vendor/enum";
import { verifySecuretedCode } from "@/vendor/globalVerify";
import { cryptoMixins, cryptoTradingMixins } from "@/vendor/mixins";
export default {
  components: {
    CryptoTradingAnnouncement,
  },
  mixins: [cryptoMixins, cryptoTradingMixins],
  data() {
    return {
      orderType: "TronTransferBusiness",
      columns: CRYPTO_WEB,
      showPicker: false,
      form: {
        paymentType: "",
        amount: "",
      },
    };
  },
  computed: {
    howMuchRMB() {
      const { amountFormat } = this.$root.$options.filters;
      return amountFormat(
        this.form.amount && this.cryptoAccountInfo.otcTicker
          ? this.form.amount * this.cryptoAccountInfo.otcTicker
          : 0,
        { places: 0 }
      );
    },
    ratio() {
      if (!this.form.paymentType) {
        return "汇率:￥0.00";
      } else {
        return `${this.form.paymentType}价格:￥${this.cryptoAccountInfo.otcTicker}`;
      }
    },
  },
  methods: {
    chooseCrypto() {
      if (this.isTrading) return;
      this.showPicker = true;
    },
    // 选择币种
    async onConfirm(val) {
      const { ok, msg } = await this.businessGetAccountInfo(val);
      if (ok) {
        this.form.amount = "";
        this.form.paymentType = val;
        this.showPicker = false;
        this.updateQRcode();
      } else {
        this.$toast(msg);
      }
    },
    //  区块链转入
    async businessDeposit() {
      // 验证资金密码
      const securePassword = await verifySecuretedCode(this);
      const { ok, msg } = await businessDeposit({
        paymentType: this.form.paymentType,
        amount: parseFloat(this.form.amount).toFixed(2),
        passageType: "TronTransferBusiness",
        receivingAddress: this.cryptoAccountInfo.address,
        securePassword,
      });
      if (ok) {
        this.$toast("订单创建成功");
        this.getDepositDetail();
      } else {
        this.$toast(msg);
      }
    },
  },
};
</script>

