    margin: 0 auto;
  }

  .notice {
    font-size: 14px;
    text-align: center;
    margin: 10px 0;
    padding-top: 10px;
  }

  .copyBtn {
    &.copy {
      height: 24px;
      padding: 0 10px;
    }

    margin-left: auto;

    &::after {
      background-color: #999999;
    }

    &::after,
    &::before {
      border-color: #999999;
    }
  }

  .van-cell {
    &__title {
      padding-left: 5px;
    }
    &__value {
      flex: 2;
      .van-field {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
}
</style>
